/* - Very dark desaturated blue: hsl(238, 29%, 16%)
- Soft red: hsl(14, 88%, 65%) #### Gradient 
Background gradient: 
- Soft violet: hsl(273, 75%, 66%) 
- Soft blue: hsl(240, 73%, 65%) */
/* #### Text
- Very dark grayish blue: hsl(237, 12%, 33%)
- Dark grayish blue: hsl(240, 6%, 50%) #### Dividers 
- Light grayish blue: hsl(240, 5%, 91%) */
@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}
body {
    background: linear-gradient(hsl(273, 75%, 66%),hsl(240, 73%, 65%)) no-repeat;
    min-height: 100vh;
    text-align: center;
    font-family: 'Kumbh Sans', sans-serif;
    overflow: hidden;
    position: relative;
    user-select: none;
}
.main-part {
    background: whitesmoke;
    position: absolute;
    width: 60%;
    border-radius: 20px;
    height: 68%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    overflow: hidden;
}

img {
    position: absolute;
}
#woman {
    width: 50%;
    left: -5%;
    top: 15%;
    z-index: 2;
}
#background {
    left: -70%;
    top: -50%;
    z-index: 1;
}
#box {
    width: 12%;
    left: 14%;
    top: 43%;
    z-index: 3;
}
.text-part {
   
    width: 50%;
    height: 100%;
    transform: translateX(100%);
    padding: 25px;
}
h1 {
    text-align: left;
    margin-top: 20px;
    margin-left:20px ;
    margin-bottom: 20px;
}
ul {
    margin-left: 20px;
    text-align: left;
}
li {
    margin-top: 25px;
    border-bottom: 0.5px solid rgb(154, 148, 148);
    padding-bottom: 20px;
    width: 100%;
    position: relative;
}
span {
    font-weight: 400;
    cursor: pointer;
    transition: 0.3s ease;
}
span.active {
    font-weight: 700;
}
span:hover {
    color: rgb(255, 123, 0);
}
p {
    font-size: 0.8em;
    padding-top: 5px;
    color: gray;
    display: none;
}
p.active {
    display: block;
}
.arrow {
 position: absolute;
 right: 0;
 top:20%;
}
.arrow.active {
    top: 5%;
    transform: rotate(180deg);
}
@media screen and (max-width: 1280px) {
    span {
        font-size: 0.8em;
    }
    p {
        font-size: 0.6em;
    }
}
@media screen and (max-width: 1050px) {
    .main-part {
        width: 40%;
        height: 80%;
        overflow: visible;
    }
    .text-part {
        margin: 0 auto;
        width: 100%;
        transform: translate(0);
    }
    #background {
        display: none;
    }
    #woman {
        left: 32%;
        top: -12%;
        width: 40%;
        filter: drop-shadow(0px 15px 4px #6161767d);
    }
    #box {
        left: 41%;
        top: 6%;
        width: 8%;
    }
    h1 {
        text-align: center;
        margin-top: 40px;
    }
    span {
        font-size: 0.7em;
    }
    ul {
        margin: 0 auto;
        width: 80%;
    }
    .arrow {
        right: -5%;
    }
}
@media screen and (max-width: 700px) {
    .main-part {
        height: 90%;
    }
    span {
        font-size: 0.5em;
    }
    p {
        font-size: 0.4em;
    }
    li {
        margin-top: 45px;
    }
}